.test-finished-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 86%;
  min-height: 600px;
}

.test-finished-title h2 {
  color: var(--finished-test-color);
  font-family: "Quicksand", sans-serif;
  margin-bottom: 10px;
  word-spacing: 2px;
  cursor: default;
}

.charts-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: clamp(800px, 60%, 1920px);
  margin-bottom: 1%;
}

.wpm-chart {
  display: flex;
  width: 80%;
  padding-top: 20px;
}

.errors-chart {
  display: flex;
  padding: 20px;
  width: 20%;
  max-height: 500px;
}

.errors-chart Line {
  max-height: 500px;
}

.others-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  align-items: center;
  width: clamp(875px, 60%, 1200px);
  margin-bottom: 0.5%;
}

.other-stat {
  background: var(--practice-code-background-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: clamp(75px, 5vw, 100px);
  padding: 10px 15px;
  margin: 3%;
  border-radius: 12px;
  cursor: default;
}

.other-stat h3 {
  color: var(--finished-test-color);
  background: inherit;
  word-spacing: -3px;
  font-size: clamp(14px, 1.05vw, 20px);
}

#test-details {
  margin-bottom: 6px;
}

#test-type {
  margin-bottom: 6px;
}

.other-stat p {
  color: var(--finished-test-secondary-color);
  background: inherit;
  font-size: 77%;
  word-spacing: -3px;
}

.other-stat div {
  color: var(--finished-test-secondary-color);
  background: inherit;
  font-size: 34px;
  font-weight: 600;
}

.finished-test-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.finished-test-button {
  color: var(--finished-test-secondary-color);
  background: var(--practice-code-background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  width: clamp(200px, 12vw, 400px);
  padding: 5px 20px;
  margin: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  border: none;
  border-radius: 12px;
  transition: all 300ms ease;
  margin: 0px 5px;
}

.finished-test-button:hover {
  background: var(--finished-test-color);
  color: var(--practice-code-background-color);
}

.finished-test-buttons-hover {
  background: var(--finished-test-color);
  color: var(--practice-code-background-color);
}

.finished-test-buttons button > * {
  background: inherit;
}

#next-icon {
  margin-top: 1px;
  font-size: 18px;
}

#prev-icon {
  margin-top: 1px;
  font-size: 16px;
  margin-left: 2px;
}

.button-label {
  margin-right: 5px;
}

.button-label span {
  background: inherit;
  font-weight: 600;
}

#typed {
  color: var(--finished-test-color);
}

#finished-test-input {
  opacity: 0;
  width: 0px;
  height: 0px;
  position: absolute;
}
